@import 'src/sass/variables';
@import 'src/sass/mixins';

.wp-block-cab-list {
  padding-top:75px;
  padding-bottom: 75px;
  padding-left: 130px;

  .cab-list-title{
    color: #111111;
    font-size: 38px;
    font-family: $fontFamily;
    font-style: italic;
    font-weight: 400;
    word-wrap: break-word;
    margin-top: -4px;
    margin-left: -3px;

    &::before{
      content: '';
      background: url("img/Decoration.svg") no-repeat;
      display: inline-block;
      height: 9px;
      position: relative;
      width: 28px;
      margin-bottom: 15px;
    }
  }
  .cab-list-description{
    margin-left: 61px;
    margin-top: 26px;
    min-width: 233px;
    color: #9D8888;
    font-size: 16px;
    font-family: $fontFamilyAdditional;
    font-weight: 400;
    line-height: 26.54px;
    word-wrap: break-word;
  }
}
@media only screen and (max-width: 900px){
  .wp-block-cab-list {
    padding: 30px 24px;
  }
}
@media only screen and (max-width: 1440px) and (min-width: 900px) {
  .wp-block-cab-list {
    .cab-list-description {
      max-width: 233px;
    }
  }
}

